import { memo } from 'react';
import { Card } from "@/components/ui/card"
import { MapPin, Mail } from "lucide-react"
import { FormData } from '@/hooks/useJobPostingForm';

interface PostPreviewProps {
  formData: FormData;
  imageUrl?: string | null;
}

// Función para calcular el tamaño del texto basado en la prioridad
const calculateFontSize = (priority: number, baseSize: number, factor: number): string => {
  return `${Math.min(baseSize + priority * factor, baseSize * 2)}rem`;
};

// Memoizamos el componente para evitar renderizados innecesarios
const PostPreview = memo(function PostPreview({ formData, imageUrl }: PostPreviewProps) {
  // Split requirements into an array for better formatting
  const requirementsList = formData.requirements
    ? formData.requirements.split("\n").filter((req) => req.trim() !== "")
    : [];

  // Formato de fecha actual para mostrar en el post
  const currentDate = new Date().toLocaleDateString('es-ES', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });

  // Si hay una imagen proporcionada por el backend, usarla, de lo contrario mostrar la vista previa local
  const displayImageUrl = imageUrl || formData.image;

  return (
    <Card className="overflow-hidden shadow-lg">
      {imageUrl ? (
        // Si hay una imagen generada por el backend, mostrarla directamente
        <div className="w-full h-full">
          <img 
            src={imageUrl} 
            alt="Vista previa generada por el servidor" 
            className="w-full h-auto"
          />
        </div>
      ) : (
        // Si no hay imagen del backend, mostrar la vista previa como antes
        <div className="relative bg-white p-0">
          {/* Preview Header */}
          <div className="bg-[#0066cc] text-white text-center py-3 sm:py-4 px-4 sm:px-6">
            <h2 className="text-lg sm:text-xl font-bold">Buscamos Profesionales</h2>
          </div>

          {/* Main Content */}
          <div className="p-4 sm:p-6">
            {/* Position */}
            {formData.position && (
              <div className="text-center mb-4 sm:mb-6">
                <h3 
                  style={{ 
                    fontSize: calculateFontSize(formData.positionPriority, 1.25, 0.15),
                    lineHeight: "1.2"
                  }} 
                  className="font-bold text-[#0066cc]"
                >
                  {formData.position}
                </h3>
              </div>
            )}

            {/* Location */}
            {formData.location && (
              <div className="flex items-center justify-center text-gray-700 mb-4 sm:mb-6">
                <MapPin className="h-4 w-4 mr-1 text-[#0066cc]" />
                <span 
                  className="font-medium"
                  style={{ 
                    fontSize: calculateFontSize(formData.locationPriority, 1, 0.1) 
                  }}
                >
                  {formData.location}
                </span>
              </div>
            )}

            {/* Requirements */}
            {requirementsList.length > 0 && (
              <div className="mb-4 sm:mb-6 text-gray-800">
                <h4 
                  className="font-semibold mb-2 text-center"
                  style={{ 
                    fontSize: calculateFontSize(formData.requirementsPriority, 1, 0.1) 
                  }}
                >
                  Requisitos:
                </h4>
                <ul className="list-disc list-inside space-y-1 text-sm sm:text-base">
                  {requirementsList.map((requirement, index) => (
                    <li key={index}>{requirement}</li>
                  ))}
                </ul>
              </div>
            )}

            {/* Contact */}
            {formData.email && (
              <div className="flex flex-col sm:flex-row items-center justify-center gap-2 text-gray-700 mt-4">
                <Mail className="h-4 w-4 text-[#0066cc]" />
                <span 
                  className="font-medium text-center"
                  style={{ 
                    fontSize: calculateFontSize(formData.emailPriority, 0.875, 0.05) 
                  }}
                >
                  {formData.email}
                </span>
              </div>
            )}

            {/* Background Image */}
            {formData.image && (
              <div className="absolute inset-0 -z-10 opacity-10">
                <img src={formData.image} alt="" className="w-full h-full object-cover" />
              </div>
            )}
          </div>

          {/* Footer with Logo */}
          <div className="bg-gray-50 p-4 flex justify-between items-center">
            <div className="text-sm text-gray-500">
              <div>#{formData.position.replace(/\s+/g, '')} #Salud #Oportunidad</div>
              <div className="text-xs text-gray-400 mt-1">{currentDate}</div>
            </div>
            <div className="font-bold text-[#0066cc]">DarSalud</div>
          </div>
        </div>
      )}
    </Card>
  );
});

export default PostPreview;