"use client"

import { memo, useState } from 'react';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Instagram, Save, Clock, Calendar } from "lucide-react"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Checkbox } from "@/components/ui/checkbox"
import { Separator } from "@/components/ui/separator"
import { Popover, PopoverTrigger } from "@/components/ui/popover"
import { CalendarIcon } from "@/components/icons/calendar-icon"
import { format } from "date-fns"
import { Loader2 } from "lucide-react"
import { Facebook, Linkedin } from "lucide-react"

interface PublishConfirmationProps {
  onConfirm: (publish: boolean, scheduleTime?: Date) => void
}

const PublishConfirmation = memo(function PublishConfirmation({ onConfirm }: PublishConfirmationProps) {
  const [publishOption, setPublishOption] = useState<"now" | "later" | "save">("now");
  const [scheduleDate, setScheduleDate] = useState<string>(() => {
    // Establecer la fecha a mañana por defecto
    const tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1);
    return tomorrow.toISOString().split('T')[0];
  });
  const [scheduleTime, setScheduleTime] = useState<string>("12:00");
  const [loadingIndex, setLoadingIndex] = useState<number | null>(null);
  const [termsAccepted, setTermsAccepted] = useState(false);
  const [dateTimeValue, setDateTimeValue] = useState<Date | null>(null);

  const handleConfirm = () => {
    if (publishOption === "save") {
      onConfirm(false);
      return;
    }

    if (publishOption === "later") {
      const scheduledDateTime = new Date(`${scheduleDate}T${scheduleTime}`);
      onConfirm(true, scheduledDateTime);
      return;
    }

    // Publicar ahora
    onConfirm(true);
  };

  // Avanzar a la próxima plataforma al hacer clic en publicar
  const handlePublish = (index: number) => {
    setLoadingIndex(index);
    setTimeout(() => {
      // Simulación de publicación
      onConfirm();
      setLoadingIndex(null);
    }, 2000);
  };

  return (
    <Card className="p-4 sm:p-6">
      <h2 className="text-xl sm:text-2xl font-semibold mb-4 sm:mb-6 text-[#0066cc] flex items-center">
        <Instagram className="mr-2 h-5 w-5" /> Confirmación de Publicación
      </h2>
      
      <div className="space-y-6">
        <div className="space-y-4">
          <h3 className="text-lg font-medium text-gray-800">Seleccione Plataformas</h3>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
            <Card className="p-4 border border-gray-200 hover:border-[#0066cc] transition-colors">
              <div className="flex flex-col items-center space-y-4">
                <div className="w-12 h-12 rounded-full bg-pink-100 flex items-center justify-center">
                  <Instagram className="h-6 w-6 text-pink-600" />
                </div>
                <h3 className="font-medium">Instagram</h3>
                <Button 
                  className="w-full"
                  onClick={() => handlePublish(0)}
                  disabled={loadingIndex !== null && loadingIndex !== 0}
                >
                  {loadingIndex === 0 ? (
                    <>
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" /> Publicando...
                    </>
                  ) : (
                    'Publicar'
                  )}
                </Button>
              </div>
            </Card>
            
            <Card className="p-4 border border-gray-200 bg-gray-50 opacity-70">
              <div className="flex flex-col items-center space-y-4">
                <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center">
                  <Facebook className="h-6 w-6 text-blue-600" />
                </div>
                <h3 className="font-medium">Facebook</h3>
                <Button 
                  className="w-full bg-gray-400" 
                  disabled={true}
                >
                  Próximamente
                </Button>
              </div>
            </Card>
            
            <Card className="p-4 border border-gray-200 bg-gray-50 opacity-70">
              <div className="flex flex-col items-center space-y-4">
                <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center">
                  <Linkedin className="h-6 w-6 text-blue-700" />
                </div>
                <h3 className="font-medium">LinkedIn</h3>
                <Button 
                  className="w-full bg-gray-400" 
                  disabled={true}
                >
                  Próximamente
                </Button>
              </div>
            </Card>
          </div>
        </div>
        
        <Separator />
        
        <div className="space-y-4">
          <h3 className="text-lg font-medium text-gray-800">Programar Publicación</h3>
          
          <div className="border rounded-md p-4 bg-gray-50 opacity-70">
            <div className="flex flex-col sm:flex-row gap-4 items-start sm:items-center">
              <Popover>
                <PopoverTrigger asChild>
                  <Button
                    variant={"outline"}
                    className="w-full sm:w-auto justify-start text-left font-normal"
                    disabled={true}
                  >
                    <CalendarIcon className="mr-2 h-4 w-4" />
                    {dateTimeValue ? (
                      format(dateTimeValue, "PPP HH:mm")
                    ) : (
                      <span>Seleccionar fecha y hora</span>
                    )}
                  </Button>
                </PopoverTrigger>
              </Popover>
              
              <Button className="w-full sm:w-auto" disabled={true}>
                Programar
              </Button>
            </div>
            <p className="text-sm text-gray-500 mt-3">
              La programación estará disponible próximamente
            </p>
          </div>
        </div>
        
        <Separator />
        
        <div className="space-y-4">
          <div className="flex items-start space-x-3">
            <Checkbox 
              id="terms" 
              checked={termsAccepted}
              onCheckedChange={(checked) => setTermsAccepted(checked as boolean)}
            />
            <div className="leading-normal">
              <Label htmlFor="terms" className="font-medium">Términos y Condiciones</Label>
              <p className="text-sm text-gray-500">
                Al publicar, confirmo que tengo los derechos necesarios sobre el contenido y cumplo con las políticas de la plataforma.
              </p>
            </div>
          </div>
        </div>
      </div>
      
      <div className="flex justify-end mt-6">
        <Button variant="ghost" className="mr-2">
          Cancelar
        </Button>
      </div>
    </Card>
  )
});

export default PublishConfirmation;